"use client";

import Navbar from "@/components/common/Navbar";
import { motion } from "framer-motion";

export default function ProfileUnderReview() {
  return (
    <div
      className="min-h-screen w-full flex flex-col items-center"
      style={{ background: "var(--background)" }}
    >
      <Navbar />

      {/* Main container */}
      <div className="w-full flex justify-center mt-16 px-4">
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.28 }}
          className="w-full max-w-[540px] rounded-2xl p-8"
          style={{
            background: "var(--color-bg-neutral-50)",
            border: "1px solid var(--color-btn-teritary-200)",
            boxShadow: "0 8px 28px rgba(10,10,10,0.06)",
          }}
        >
          {/* Top Icon */}
          <div className="flex justify-center">
            <div
              className="w-16 h-16 rounded-full flex items-center justify-center"
              style={{
                background: "var(--color-bg-primary-50)",
                boxShadow: "0 6px 18px rgba(31,131,229,0.10)",
              }}
            >
              <img
                src="/wrench.svg"
                alt="Profile Review Icon"
                className="w-[38px] h-[38px] object-contain"
              />
            </div>
          </div>

          {/* Heading */}
          <h2
            className="text-[18px] font-semibold text-center mt-5"
            style={{ color: "var(--color-text-primary)" }}
          >
            Profile Under Review
          </h2>

          <p
            className="text-[13px] text-center mt-3 leading-5"
            style={{ color: "var(--color-text-secondary)" }}
          >
            Thank you for submitting your profile! Our team will review your
            details within 24–48 hours.
          </p>

          {/* Approval Time Box */}
          <div className="flex justify-center mt-5">
            <div
              className="rounded-xl w-full max-w-[420px] px-4 py-3 text-center"
              style={{
                background: "var(--color-bg-primary-50)",
                border: "1px solid var(--color-btn-teritary-200)",
              }}
            >
              <p
                className="text-[13px] font-medium"
                style={{ color: "var(--color-text-primary)" }}
              >
                Average approval time: <strong>12–18 hours</strong>
              </p>
              <p
                className="text-[12px] mt-1"
                style={{ color: "var(--color-text-muted)" }}
              >
                We’ll notify you at your <strong>registered email</strong>
              </p>
            </div>
          </div>

          {/* What Happens Next */}
          <div
            className="rounded-xl p-5 mt-6"
            style={{
              background: "var(--color-bg-secondary-50)",
              border: "1px solid var(--color-btn-teritary-200)",
            }}
          >
            <h3
              className="text-[14px] font-semibold mb-3"
              style={{ color: "var(--color-text-primary)" }}
            >
              What happens next?
            </h3>

            <div className="space-y-4">
              {/* STEP 1 — wrench.svg */}
              <div className="flex items-start gap-3">
                <div
                  className="flex items-center justify-center"
                  style={{
                    width: 30,
                    height: 30,
                    borderRadius: 20,
                    border: "1px solid var(--color-btn-teritary-200)",
                    background: "var(--color-bg-neutral-50)",
                  }}
                >
                  <img
                    src="/wrench2.svg"
                    alt="Verification Icon"
                    className="w-[29px] h-[29px] object-contain"
                  />
                </div>

                <div>
                  <p
                    className="text-[13px] font-medium"
                    style={{ color: "var(--color-text-primary)" }}
                  >
                    Verification Process
                  </p>
                  <p
                    className="text-[12px]"
                    style={{ color: "var(--color-text-muted)" }}
                  >
                    We’ll verify your documents and information
                  </p>
                </div>
              </div>

              {/* STEP 2 — verification.svg */}
              <div className="flex items-start gap-3">
                <div
                  className="flex items-center justify-center"
                  style={{
                    width: 30,
                    height: 30,
                    borderRadius: 20,
                    border: "1px solid var(--color-btn-teritary-200)",
                    background: "var(--color-bg-neutral-50)",
                  }}
                >
                  <img
                    src="/approval.svg"
                    alt="Approval Notification Icon"
                    className="w-[29px] h-[29px]  object-contain"
                  />
                </div>

                <div>
                  <p
                    className="text-[13px] font-medium"
                    style={{ color: "var(--color-text-primary)" }}
                  >
                    Approval Notification
                  </p>
                  <p
                    className="text-[12px]"
                    style={{ color: "var(--color-text-muted)" }}
                  >
                    You’ll receive an email and in-app notification
                  </p>
                </div>
              </div>

              {/* STEP 3 — approval.svg */}
              <div className="flex items-start gap-3">
                <div
                  className="flex items-center justify-center"
                  style={{
                    width: 30,
                    height: 30,
                    borderRadius: 20,
                    border: "1px solid var(--color-btn-teritary-200)",
                    background: "var(--color-bg-neutral-50)",
                  }}
                >
                  <img
                    src="/verification.svg"
                    alt="Start Jobs Icon"
                    className="w-[29px] h-[29px] object-contain"
                  />
                </div>

                <div>
                  <p
                    className="text-[13px] font-medium"
                    style={{ color: "var(--color-text-primary)" }}
                  >
                    Start Accepting Jobs
                  </p>
                  <p
                    className="text-[12px]"
                    style={{ color: "var(--color-text-muted)" }}
                  >
                    Once approved, you can start earning
                  </p>
                </div>
              </div>
            </div>
          </div>

          {/* CTA Button */}
          <button
            className="w-full py-3 rounded-lg mt-6 text-[15px] font-medium"
            style={{
              background: "var(--color-btn-primary-600)",
              color: "var(--color-bg-neutral-50)",
              boxShadow: "0 8px 18px rgba(21,93,252,0.20)",
            }}
          >
            Go to dashboard
          </button>

          {/* Footer */}
          <p
            className="text-[13px] text-center mt-4"
            style={{ color: "var(--color-text-muted)" }}
          >
            Questions? Contact support@workly.com
          </p>
        </motion.div>
      </div>
    </div>
  );
}
